.Navbar {
  display: flex;
  width: 100vw;
  height: 40px;
  align-items: center;
  justify-content: space-between;
}

.titleText {
  color: #707070;
  font-weight: normal;
  font-size: 20px;
  padding-left: 10px;
}

.bold {
  font-weight: bold;
}

.buttonText {
  color: #707070;
  font-weight: normal;
  font-size: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.buttons {
  display: flex;
  flex-direction: row;
}
